import React, { memo, useState } from "react";
import classNames from "classnames";

import { LayoutModeWrapper, MusicsWrapper } from "./style";
import KTMusicsHeader from "./c-components/musics-header";
import { musicCategory } from "../../common/music-ephemeral-data";
import KTMusicsCategory from "./c-components/musics-category";
import withWindowSize from "@/components/higher-order/withWindowSize";
import KTEvemtsListView from "@/components/event/events-list-view";
import KTMobileEventList from "@/components/mobile-components/mobile-event-list";
import { LAYOUT_MODE_CARD, LAYOUT_MODE_LIST } from "@/common/constants";
import { homeContentEventItems } from "../../common/ephemeral-data";
import { currValue } from "@/utils";
import { ReactComponent as CardLayoutIcon } from "@/assets/img/svg/icon-card-layout.svg";
import { ReactComponent as ListLayoutIcon } from "@/assets/img/svg/icon-list-layout.svg";

const KTMusics = memo(({ windowSize }) => {
  const [layoutMode, setLayoutMode] = useState(LAYOUT_MODE_CARD);

  return (
    <MusicsWrapper>
      <KTMusicsHeader />
      <KTMusicsCategory categorys={musicCategory} />
      <LayoutModeWrapper
        style={{ margin: `0 ${currValue(50, 100, windowSize.width)}` }}
      >
        {windowSize.width > 480
          ? [
              <CardLayoutIcon
                className={classNames("layoutModel", {
                  layoutModelActive: layoutMode === LAYOUT_MODE_CARD,
                })}
                onClick={() => setLayoutMode(LAYOUT_MODE_CARD)}
              />,
              <ListLayoutIcon
                className={classNames("layoutModel", {
                  layoutModelActive: layoutMode === LAYOUT_MODE_LIST,
                })}
                onClick={() => setLayoutMode(LAYOUT_MODE_LIST)}
              />,
            ]
          : layoutMode === LAYOUT_MODE_LIST
          ? [
              <ListLayoutIcon
                className="layoutModel layoutModelActive"
                onClick={() => setLayoutMode(LAYOUT_MODE_CARD)}
              />,
            ]
          : [
              <CardLayoutIcon
                className="layoutModel layoutModelActive"
                onClick={() => setLayoutMode(LAYOUT_MODE_LIST)}
              />,
            ]}
      </LayoutModeWrapper>
      {windowSize.width > 480 ? (
        <KTEvemtsListView
          style={{ margin: `18px ${currValue(50, 100, windowSize.width)}` }}
          key={layoutMode}
          events={homeContentEventItems}
          layoutMode={layoutMode}
          cardWidth={currValue(350, 400, windowSize.width)}
          windowWidth={windowSize.width}
        />
      ) : (
        <KTMobileEventList
          events={homeContentEventItems}
          layoutMode={layoutMode}
        />
      )}
    </MusicsWrapper>
  );
});

export default withWindowSize(KTMusics);
