import React, { memo } from "react";
import {
  CategoryItemWrapper,
  MusicsCategoryListWrapper,
  MusicsCategoryWrapper,
} from "./style";

import { ReactComponent as SelectedMark } from "@/assets/img/icons/icon-selected-mark.svg";

const KTMusicsCategory = memo(({ categorys }) => {
  const items = categorys.map((item) => {
    return (
      <CategoryItemWrapper>
        <img
          src={require(`@/assets/img/temporary/${item.icon}`)}
          alt=""
          className="icon"
        />
        <div className="name">{item.name}</div>
        <SelectedMark className="selectedMark" />
      </CategoryItemWrapper>
    );
  });

  return (
    <MusicsCategoryWrapper>
      <div className="selectAll">Unselect All</div>
      <MusicsCategoryListWrapper>{items}</MusicsCategoryListWrapper>
    </MusicsCategoryWrapper>
  );
});

export default KTMusicsCategory;
